import logo from "./logo.svg";
import React, { useState, useEffect, useMemo } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.scss";
import StockItem from "./components/StockItems.js";

function App() {
  const [stockData, setStockData] = useState({});
  useEffect(() => {
    fetch("https://monitortws.myftp.org/api/update_monitor", {
      method: "GET",
      header: {
        "Content-Type": "application/json",
      },
    })
      .then((response) => response.json())
      .then((data) => {
        console.log(data);
        setStockData(data);
      })
      .catch((err) => console.log(err));
  }, []);

  const StockColumn = ({ title, data = [] }) => {
    return (
      <div className="stockColumn">
        <h4 className="bg-light stockColumn-title">{title}</h4>

        {data.map((item) => {
          return <StockItem item={item} />;
        })}
      </div>
    );
  };
  const ChagneColumn = ({ data = {}, columnTitle = "" }) => {
    const title = {
      down: "跌",
      limitdown: "跌停",
      unchanged: "平",
      up: "漲 ",
      limitup: "漲停",
    };
    return (
      <div className="infoWrapper">
        <div className="infoCount">
          <div className="d-flex table-responsive">
            <table className="table table-striped">
              <thead>
                <tr>
                  {Object.entries(data).map(([key, value], index) => {
                    return (
                      <th>{title[key] ? title[key] : `${key.toString()}`}</th>
                    );
                  })}
                </tr>
              </thead>
              <tbody>
                <tr>
                  {Object.entries(data).map(([key, value], index) => {
                    return <td className={`${key}`}>{value}</td>;
                  })}
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    );
  };
  const IndexStatus = () => {
    // const {
    //   exchange,
    //   open,
    //   change_rate,
    //   close,
    //   kbar,
    //   spread,
    //   volume_ratio,
    //   high,
    //   low,
    // } = data;
    return (
      <div>
        <div className="card table-responsive">
          <table className="table table-striped">
            <thead>
              <tr>
                <th scope="col">指數</th>
                <th scope="col">收盤價</th>
                <th scope="col">開盤價</th>
                <th scope="col">最高價</th>
                <th scope="col">最低價</th>
                <th scope="col">高低差</th>
                <th scope="col">K棒</th>
                <th scope="col">漲跌幅</th>
                <th scope="col">量增率</th>
              </tr>
            </thead>
            <tbody>
              {indexstatus.map(
                ({
                  exchange,
                  open,
                  change_rate,
                  close,
                  kbar,
                  spread,
                  volume_ratio,
                  high,
                  low,
                }) => {
                  return (
                    <tr>
                      <td>{exchange}</td>
                      <td>{close}</td>
                      <td>{open}</td>
                      <td>{high}</td>
                      <td>{low}</td>
                      <td>{spread.toFixed(2)}</td>
                      <td>{kbar.toFixed(2)}</td>
                      <td>{change_rate}</td>

                      <td>{volume_ratio.toFixed(2)}</td>
                    </tr>
                  );
                }
              )}
            </tbody>
          </table>
          {/* <span>開 : {open}</span>
          <span>指數 : {change_rate}</span>
          <span>最高 : {high}</span>
          <span>最低 : {low}</span> */}
        </div>
      </div>
    );
  };
  const StockTableGroup = ({ data, title }) => {
    return (
      <div className="stockTableGroup table-responsive">
        <h4>{title}</h4>
        <table className="table table-striped">
          <thead>
            <tr>
              <th scope="col">指數</th>
              <th scope="col">漲跌幅</th>
              <th scope="col">量增率</th>
              <th scope="col">成值比</th>
            </tr>
          </thead>
          <tbody>
            {data.map(
              (
                {
                  exchange = 0,
                  change_rate = 0,
                  volume_ratio = 0,
                  amount_ratio = 0,
                },
                i
              ) => {
                return (
                  <tr>
                    <td>{exchange}</td>
                    <td>{change_rate}</td>
                    <td>{volume_ratio}</td>
                    <td>{amount_ratio}</td>
                  </tr>
                );
              }
            )}
          </tbody>
        </table>
      </div>
    );
  };
  const {
    amount_rank,
    changetype_count,
    n_attack,
    p_attack,
    pmom_stocks,
    indexstatus = [],
    top_mv,
    nmom_stocks,
    pmom_groups = [],
    nmom_groups = [],
    future_diff = "",
  } = stockData;

  return (
    <div className="App d-flex  flex-column" style={{ maxHeight: "100vh" }}>
      <header className="container-fluid flex-grow-1">
        <div className="row  d-flex justify-content-around">
          <div className="col-3 card headerInner">
            <div>
              <StockTableGroup title={"正個股"} data={pmom_groups} />
              <StockTableGroup title={"負個股"} data={nmom_groups} />
            </div>
          </div>

          <div className="col-6 card headerInner">
            <h4>指數數據</h4>
            <div>
              <IndexStatus />
            </div>
            <h4> 期現貨差</h4>
            <div>{future_diff}</div>
            <h4> 漲跌家數</h4>
            <div className="card columnStatus">
              <ChagneColumn
                columnTitle="漲跌家數"
                data={changetype_count}
              ></ChagneColumn>
            </div>
          </div>
          <div className="col-3 card headerInner">
            <StockColumn title={"攻擊個股"} data={n_attack}></StockColumn>
            <StockColumn title={"攻擊個股"} data={p_attack}></StockColumn>
          </div>
        </div>
      </header>
      <div className="container-fluid " style={{ flexGrow: 2 }}>
        <div
          className="row d-flex justify-content-around align-items-stretch"
          style={{ minHeight: 1 }}
        >
          <div className="col-6 col-md-3 card appColumnWrapper">
            <StockColumn title={"成值排行"} data={amount_rank}></StockColumn>
          </div>
          <div className="col-6 col-md-3 appColumnWrapper">
            <StockColumn title={"正族群"} data={pmom_stocks}></StockColumn>
          </div>
          <div className="col-6 col-md-3 appColumnWrapper">
            <StockColumn title={"負族群"} data={nmom_stocks}></StockColumn>
          </div>
          <div className="col-6 col-md-3 appColumnWrapper">
            <StockColumn title={"市值排行"} data={top_mv}></StockColumn>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
